/*------------------------------------------------------------------
1. Basic Cards
--------------------------------------------------------------------*/
.card .badge {
  font-size: 0.75rem;
  padding: 0.25rem 0.75rem;
  margin-top: 8px;
  line-height: 1.5;
}

.icon-small {
  font-size: 1.5rem;
}

.icon-base {
  font-size: 2rem;
}

.icon-large {
  font-size: 2.5rem;
}

.icon-xl {
  font-size: 3rem;
}

.icon-xxl {
  font-size: 5rem;
}

.uds-img {
  opacity: 1;
  margin-bottom: 0rem;
  max-width: 900px;
}

.uds-img img {
  border: 1px solid #d0d0d0;
  width: 100%;
}

.uds-img .uds-figure {
  width: 100%;
}

.uds-img .uds-figure img.img-original {
  width: initial;
}

.uds-img .uds-figure .uds-figure-caption {
  border: 1px solid #d0d0d0;
  border-top: none;
  opacity: 1;
  background: #ffffff 0% 0% no-repeat padding-box;
  padding: 0.75rem;
  font-size: 0.75rem;
}

.uds-img .uds-figure .uds-caption-text {
  display: block;
  max-width: 75ch;
  color: #747474;
}

.uds-img.uds-img-drop-shadow {
  -webkit-box-shadow: 0rem 0.5rem 1rem rgba(25, 25, 25, 0.2);
          box-shadow: 0rem 0.5rem 1rem rgba(25, 25, 25, 0.2);
}

.uds-img.uds-img-drop-shadow .uds-figure {
  margin-bottom: 0rem;
}

/*--------------------------------------------------------------
# Content Image Overlap

1. Mobile
2. Desktop lg
3. Desktop xl
--------------------------------------------------------------*/
.uds-image-overlap {
  position: relative;
  padding-top: 1.5rem;
}

.uds-image-overlap:before {
  content: '';
  height: 1.5rem;
  width: calc(100% - 4rem);
  background-color: #ffc627;
  position: absolute;
  left: 2rem;
  top: 0;
}

/*------------------------------------------------------------------
2. Desktop, large
------------------------------------------------------------------- */
@media (min-width: 992px) {
  .uds-image-overlap {
    display: grid;
    grid-template-columns: 1.5rem 1fr 10rem 1fr 10rem 1fr 1.5rem;
    grid-template-rows: minmax(0, auto) minmax(0, 2rem) 1fr minmax(0, 2rem) minmax(0, auto);
    gap: 0px 0px;
  }
  .uds-image-overlap:before {
    display: none;
  }
  .uds-image-overlap img {
    width: 100%;
    height: auto;
    grid-column: 2 / span 4;
    grid-row: 2 / 5;
  }
  .uds-image-overlap .content-wrapper {
    grid-column: 5 / span 3;
    grid-row: 3 / 4;
    background-color: #ffffff;
    padding: 2rem;
  }
  .uds-image-overlap:after {
    content: '';
    width: 1.5rem;
    background-color: #ffc627;
    grid-column: 1 / 2;
    grid-row: 3 / span 1;
  }
  .uds-image-overlap.content-left img {
    grid-column: 3 / span 4;
    grid-row: 2 / 5;
  }
  .uds-image-overlap.content-left .content-wrapper {
    grid-column: 1 / span 3;
    grid-row: 3 / 4;
  }
  .uds-image-overlap.content-left:after {
    grid-column: 7 / 8;
    grid-row: 3 / span 1;
  }
}

/*------------------------------------------------------------------
2. Desktop, XL
--------------------------------------------------------------------*/
@media (min-width: 1260px) {
  .uds-image-overlap {
