}

.scroll-control-next {
  position: absolute;
  height: 100%;
  right: 0;
  top: 0;
  background: rgba(0, 0, 0, 0);
  background: -webkit-gradient(linear, left top, right top, from(rgba(25, 25, 25, 0)), to(rgba(25, 25, 25, 0.25)));
